.title {
  margin-top: 20px;
  margin-bottom: 40px;
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-stretch: normal;
  line-height: 36px;
  text-align: left;
  font: normal normal 600 20px/27px Open Sans !important;
  letter-spacing: 0px !important;
  color: #000000 !important;
  opacity: 1;
}

.tit {
  margin-top: -40px;
  margin-bottom: 50px;
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: normal;
  font-stretch: normal;
  font-size: 14px;
  line-height: 19px;
}

@media (min-width: 768px) {
  .title {
    text-align: center;
  }

  .tit {
    text-align: center;
  }
}

.inputotp {
  display: flex;

  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
.inputwrapper {
  flex: 0 0 16.667%;
  max-width: 16.667%;

  padding: 0 15px;
}
.input {
  border: none;
  border-bottom: 1px solid #8b8b8b;
  text-align: center;
  width: 100%;
}
.input:focus {
  outline: none;
}

.label {
  margin-bottom: 15px;
  color: #333333de;
  font-size: 13px;
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: 600;
  font-stretch: normal;
  line-height: 18px;
}

.span {
  cursor: pointer;
  color: #1ba6e1;
}

.p {
  font-size: 12px;
  margin-top: 15px;
}

.btn {
  background-color: #1ba7e2 !important;
  border-color: #1ba7e2 !important;
  margin-top: 50px;
}

.resendBtn {
  border: none !important;
  padding: 0 !important;
  font-size: 12px !important;
  text-decoration: none !important;
  font-weight: 500 !important;
  outline: none !important;
  box-shadow: none !important;
}

.resendBtn :hover {
  text-decoration: none !important;
}

.errorMessage {
  color: red;
  font-size: 0.8rem;
  font-weight: 400;
  margin-top: 8px;
}
