.articlecard {
  margin: 1rem 0;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 1rem;
}
.taxo {
  display: block;
  color: #f2541a;

  font-size: 0.625rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
  margin: 0;
  margin-bottom: 0.6rem;
  margin-top: 0rem;
}
@media (min-width: 768px) {
  .taxo {
    margin-top: 0rem;
    font-size: 0.875rem;
  }
}
.taxo:hover {
  text-decoration: none;
}

.title {
  display: block;
  font-size: 1rem;
  background-color: transparent;
  border: none;
  font: normal normal 600 18px/28px Open Sans;
  font-weight: 600;
  line-height: 1.25rem;
  margin: 0px;
  text-align: left;
  padding: 0px !important;
  margin-bottom: 0.4rem;
  opacity: 1;
  color: #000000;
  text-decoration: none !important;
  outline: none !important;
}

.title:hover {
  text-decoration: none !important;
  outline: 0;
}

.title:focus {
  text-decoration: none !important;
  outline: none !important;
}

@media (min-width: 768px) {
  .title {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
}

.author {
  font: normal normal normal 16px/24px Roboto;
  opacity: 0.7;
  letter-spacing: 0px;
  color: #9b9b9b;
  font-size: 0.725rem !important;
  line-height: 1.5rem;
}

@media (min-width: 768px) {
  .author {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}

.excerpt {
  font: normal normal normal 16px/24px Roboto;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.7;
  font-size: 0.75rem;
  line-height: 1.125rem;
  margin-bottom: 1.6rem;
  text-align: justify;
  margin-top: 10px;
}

.excerpt:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .excerpt {
    font-size: 1rem;
    line-height: 1.5rem;
    min-height: 8rem !important;
  }
}

.footer p {
  margin: 0;
}
.footer .left {
  float: left;
}
.footer .right {
  float: right;
}
.bookmarkleft {
  font: normal normal medium 14px/18px Roboto;
  opacity: 0.7;
  letter-spacing: 0.7px;
  color: #9b9b9b;
  padding-left: 0px;
  font-size: 0.625rem;
  line-height: 1.125rem;
  background-color: transparent;
  border: none;
  outline: none !important;
}

@media (min-width: 768px) {
  .bookmarkleft {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}

.bookmarkleft svg {
  position: relative;
  top: -3px;
}
.bookmarkleft:hover {
  text-decoration: none;
  color: #007bff;
}
.publishedright {
  font-size: 0.625rem;
  line-height: 1.125rem;
  color: #9b9b9b;
  float: right;
  font-weight: 500;
  padding-top: 0.46rem;
}

@media (min-width: 768px) {
  .publishedright {
    padding-top: 0.27rem;
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}

.articlecard hr {
  margin-top: 1rem;
}

.sourceRelevance {
  display: list-item;
  justify-content: flex-start;
  list-style-type: none;
}

.source {
  text-align: left;
  margin-top: 2px;
  margin-bottom: 4px;
  font: normal normal normal 11px/18px Roboto;
  letter-spacing: 0.03rem;
  color: #707070;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  opacity: 0.8;
  font-size: 0.7rem;
  padding-right: 1.2rem;
}

.bull {
  display: none;
  color: #707070;
  margin-right: 1.2rem;
}

@media (min-width: 768px) {
  .sourceRelevance {
    display: flex;
    justify-content: flex-start;
  }

  .source {
    font-size: 0.85rem;
    line-height: 1.5rem;
  }

  .bull {
    display: block;
  }
}

.high {
  color: #47be38;
}

.low {
  color: #c94728;
}

.medium {
  color: #de9c1e;
}

.sourceLink {
  text-align: left;
  text-decoration: underline;
  color: #245bd6;
}
