.label {
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: 600;
  font-stretch: normal;
  font-size: 13px;
  line-height: 18px;
  color: #333333de;
  opacity: 1;
  font-family: "Open Sans";
}

.pageTitle {
  font-weight: 500;
  color: black;
  text-align: left;
  margin-top: 5px;
  margin-bottom: 20px;
  font: normal normal 600 20px/36px Open Sans;
}

@media (min-width: 768px) {
  .pageTitle {
    text-align: center;
    font-size: 26px;
  }
}

.span {
  background-color: transparent;
}

.btn {
  border-color: #1ba7e2 !important;
  margin-top: 30px !important;
  text-align: center;
  font: normal normal bold 14px/19px Open Sans;
  letter-spacing: 1.4px;
  color: #ffffff;
  text-transform: uppercase;
  opacity: 1;
}

.links {
  margin-top: 4px;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
}

.errorMessage {
  color: red;
  font-size: 0.8rem;
  font-weight: 400;
  margin-top: -10px;
  padding: 0px 3px;
}

@media (max-width: 320px) {
  .links {
    font-size: 12px;
  }
}

@media (max-width: 375px) {
  .links {
    font-size: 13px;
  }
}
