.overlayContainer {
  background: rgba(107, 107, 107, 0.699);
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  border-top: white !important;
  /* padding: none !important; */
  padding: 0px !important;
}
.btnContainer {
  display: flex;
  justify-content: flex-end;
  /* border-top: white !important; */
}

.modal {
  background: white;
  /* padding: 3rem 2rem; */
  padding-left: initial;
  border-radius: 4px;
  opacity: 1;
}

.alert {
  font: normal normal 600 18px/28px Open Sans;
  letter-spacing: 0px;
  color: #000000 !important;
  opacity: 1;
  background-color: #e3f2f8 !important;
  border-radius: 4px;
  border-color: transparent !important;
}

/*.mymodal {
 
  width:80%; 
  top:20%;
}*/

.cancelBtn {
  font: normal normal bold 14px/24px Open Sans;
  letter-spacing: 1.4px;
  color: #000000 !important;
  text-transform: uppercase;
  opacity: 0.3;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  padding: 4px 12px;
  margin: 4px;
  outline: none !important;
}

.proceedBtn {
  font: normal normal bold 14px/24px Open Sans;
  letter-spacing: 1.4px;
  color: #ffffff !important;
  text-transform: uppercase;
  opacity: 1;
  border: none;
  border-radius: 4px;
  padding: 4px 12px;
  background-color: #3eb8ee;
  margin: 4px;
  outline: none !important;
}

@media (max-width: 768px) {
  .modal {
    background: white;
    /* padding: 3rem 2rem; */
    padding-left: initial;
    border-radius: 4px;
    opacity: 1;
    /* width: 87%; */
    /* background: white; */
    /* padding: 3rem 1.5rem; */
    /* border-radius: 5px; */
  }
  .alert{

    font: normal normal 600 16px/28px Open Sans !important;
    justify-content: center;
    font : auto;
    letter-spacing: 0px;
    color: #000000 !important;
    opacity: 1;
    background-color: #e3f2f8 !important;
    border-radius: 4px;
    border-color: transparent !important;
  }

  .footer{
    padding: 0px !important;

  }

  .cancelBtn {
    font: normal normal bold 14px Open Sans !important;
    letter-spacing: 1.4px;
    color: #000000 !important;
    text-transform: uppercase;
    opacity: 0.3;
    font-weight: 700;
    border: none;
    border-radius: 4px;
    padding: 4px 12px;
    margin: 4px;
    outline: none !important;
  }
  
  .proceedBtn {
    font: normal normal bold 14px/24px Open Sans !important;
    letter-spacing: 1.4px;
    color: #ffffff !important;
    text-transform: uppercase;
    opacity: 1;
    border: none;
    border-radius: 4px;
    padding: 4px 12px;
    background-color: #3eb8ee;
    margin: 4px;
    outline: none !important;
  }

  
}

