.page_title {
  font-size: 20px;
  line-height: 27px;
  margin-top: 40px;
  margin-bottom: 25px;
  font-weight: 600;
}

p {
  font: normal normal 500 16px/24px Open Sans;
  text-align: justify;
}

.section_desc {
  color: #666666;
  font-size: 16px;
  text-align: justify;
  line-height: 24px;
}

@media (min-width: 768px) {
  .page_title {
    font-size: 30px;
    line-height: 41px;
  }

  .section_desc {
    font-size: 18px;
    line-height: 28px;
  }
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.iconText {
  margin-top: 35.35px;
  width: 199px;
  text-align: center;
  padding: 20px;
  font-weight: bold;
}

.joinTextSub {
  text-transform: uppercase;
  font-size: 20px;
  color: #f2541a;
  text-align: center;
  font-weight: bold;
}

.dot {
  display: inline-block;
  width: 3px;
  height: 3px;
  background: #f2541a;
  border-radius: 50%;
  margin: 0px 15px;
  transform: translateY(-100%);
}

.bulletPoints {
  /* display : flex;
  align-content: center; */
}

.bulletText {
  margin-left: 15px;
}

.hideLarge {
  margin-bottom: 53px;
  margin-top: -50px;
}

@media (min-width: 768px) {
  .hideLarge {
    display: none;
    margin-bottom: 30px;
  }
}

@media (max-width: 768px) {
  .hideSmall {
    display: none;
  }

  .gccmctext {
    margin-top: 40px !important;
  }

  .joinTextSub {
    font-size: 14px;
  }
}

.tick {
  width: 13px;
}

.firstPara {
  font-weight: initial;
}

@media (min-width: 768px) {
  .firstPara {
    padding-top: 4.2rem;
  }
}
