.hero {
  background: #e3f2f8 0% 0% no-repeat padding-box;
  /* background-color: #ffffff; */
  padding: 2rem 0 1.25rem 0;
  margin-bottom: 0.8rem;
  margin-top: 85px;
}

.heroWithMinSearch {
  margin-top: 0px;
  padding: 1rem 0 1.25rem 0;
}

.hero.searchbox {
  background-color: #e3f2f8;

  border-radius: 4px;
  padding: 1.25rem 0;
}
@media (min-width: 768px) {
  .hero {
    background-color: #e3f2f8;
    padding: 1.5rem 0 1.2rem 0;
  }
}
.hero .content h1 {
  text-align: center;
  font-size: 1.15rem;
  line-height: 1.5rem;
  width: 70%;
  font-weight: 600;
  color: #1ba7e2;
  padding: 0px 4%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .hero .content h1 {
    font-size: 1.8rem;
    line-height: 2.5rem;
    width: 100%;
  }
}
.hero .content .keywords {
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 0.5rem;
  opacity: 0.7;
}
.hero .content .keywords li {
  display: inline;
  color: #f2541a;
  font-size: 0.75rem;
  font-weight: bold;
  line-height: 1.5rem;
}
@media (min-width: 768px) {
  .hero .content .keywords li {
    font-size: 1rem;
    font-weight: bold;
  }
}
.hero .content .keywords li::after {
  content: "|";
  margin: 0 0.5rem;
}
@media (min-width: 768px) {
  .hero .content .keywords li::after {
    margin: 0 2rem;
  }
}
.hero .content .keywords li:last-child::after {
  display: none;
}
.hero .searchbox {
  margin-top: 0.6rem;
  margin-bottom: 0px !important;
  /* height: 3px; */
}
.hero .searchbox .input-group-text {
  background-color: white;
  border: none;
}
.hero .searchbox .formcontrol {
  padding: 1.5rem 0rem 1.5rem 1rem;
  border: none;
}
@media (min-width: 768px) {
  .hero .searchbox .formcontrol {
    padding: 1.5rem 2rem;
  }
}
.hero .searchbox .formcontrol:focus {
  outline: none;
  box-shadow: none;
}
