.container {
  margin-top: 90px;
}

.pageTitle {
  font: normal normal 600 16px/24px Open Sans;
  font-size: 24px;
  line-height: 27px;
  margin-top: 40px;
  font-weight: 600;
  margin-bottom: 25px;
}

.sectionTitle {
  text-align: left;
  font-size: 20px;
  letter-spacing: 0px;
  color: #1ba7e2;
  font-weight: 600;
  opacity: 1;
  margin-bottom: 20px;
  margin-top: 1.2rem;
}

.sectionDesc {
  color: #666666;
  font-size: 16px;
  text-align: justify;
  line-height: 24px;
  overflow-wrap: break-word;
}

.list {
  color: #666666;
  font-size: 16px;
  text-align: justify;
  line-height: 24px;
  font-weight: 600;
}

.locations {
  background-color: #f9f9f9;
  padding: 5px 0 50px 0;
  margin-top: 30px;
}

.links {
  padding: 0;
  margin: 1rem 0;
  list-style: none;
}

.links li {
  margin: 40px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
  text-align: left;
  font: normal normal normal 18px/28px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  /* text-transform: capitalize; */
  opacity: 1;
}

.links li span {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  max-width: 100%;
  font-weight: 600;
}

.links li span:last-child {
  flex-grow: 1;
}

.links li a {
  color: #1ba7e2;
}

.phoneNumbers {
  color: #1ba7e2 !important;
  font-weight: 600;
}

@media (min-width: 768px) {
  .pageTitle {
    font-size: 30px;
    line-height: 41px;
  }

  .sectionTitle {
    font-size: 24px;
  }

  .sectionDesc {
    font-size: 18px;
    line-height: 28px;
  }

  .list {
    font-size: 18px;
    line-height: 28px;
  }
}
