header {
  padding: 25px 0;
  margin-bottom: 25px;
  background-color: #e3f2f8;
}

.headertitle {
  font-size: 14px;
  font-weight: 500;
  margin-top: 10px !important;
  text-align: center;
  font: normal normal bold 14px/19px Open Sans !important;
  letter-spacing: 1.4px !important;
  color: #f2541a !important;
  opacity: 1 !important;
}

.logo {
  cursor: pointer;
  height: auto;
  width: 80%;
}

@media (min-width: 768px) {
  header {
    background-color: #ffffff;
  }

  .logo {
    width: 65%;
  }
}
