.navbarContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 87px;
  width: 100vw;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 0px 3px 10px #0000000d;
  transition: all 0.3s ease-in-out;
}

.normalNav {
  background-color: #e3f2f8;
  box-shadow: none;
}

/* for mobile screens */
.navbar {
  width: 100vw;
  padding: 0px 8px;
}

.main {
  display: flex;
  justify-content: space-between;
}

.logo {
  max-width: 120px;
  height: auto;
  margin-top: 7px;
}

.logo:hover {
  cursor: pointer;
}

.navLinks {
  display: flex;
  align-items: center;
}

.navLinks > ul {
  padding-top: 14px;
}

.navLinks > ul li {
  display: none;
  padding: 8px;
  margin: 0px 8px;
  text-align: left;
  font: normal normal bold 14px/24px Open Sans;
  letter-spacing: 1.4px;
  color: #1ba6e1;
  text-transform: uppercase;
}

.hamburgerBtnItem {
  display: inline-block;
}

.hamburgerBtn {
  outline: none !important;
  color: #1ba6e1;
  background-color: transparent;
  border: none;
  margin-left: 1vw;
}

.navLinks > ul li a {
  text-decoration: none;
  color: #1ba6e1;
}

.navLinks > ul li a:hover {
  text-decoration: none;
  color: #1ba6e1;
}

.primaryBtn {
  background-color: #1ba7e2;
  border-radius: 4px;
  padding: 8px 14px;
  color: #ffffff !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.dropdowncontent {
  display: none;
  position: absolute;
  top: 1.1rem;
  left: 2rem;
  background-color: #ffffff;
  width: fit-content;
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
  padding: 12px 24px;
  z-index: 101;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
}

.dropdown:hover .dropdowncontent {
  display: block;
}

.dropdowncontent a {
  display: block;
  margin: 8px 0px;
}

/* for tablet */
@media only screen and (min-width: 768px) {
  .dropdowncontent {
    top: 1.2rem;
    left: 1rem;
  }

  .navbar {
    width: 94vw;
    padding: 0px 0vw 0px 2.5vw;
  }

  .miniMenu > ul li {
    margin-left: 1vw !important;
  }

  .navLinks > ul li {
    display: inline-block;
    padding: 8px;
    margin: 0px 8px;
    /* border: 1px solid green; */
    text-align: left;
    font: normal normal bold 14px/24px Open Sans;
    letter-spacing: 1.4px;
    color: #1ba6e1;
    text-transform: uppercase;
  }

  .navLinks > ul li a {
    text-decoration: none;
    color: #1ba6e1;
  }

  .navLinks > ul li a:hover {
    text-decoration: none;
    color: #1ba6e1;
  }

  .hamburgerBtnItem {
    display: none;
  }

  .miniMenu {
    display: none !important;
  }

  .miniSearch {
    display: none !important;
  }
}

/* for laptop */
@media only screen and (min-width: 1024px) {
  .navbar {
    width: 93vw;
    padding: 0px 0vw 0px 1vw;
  }
}

/* for bigger screens */
@media only screen and (min-width: 1440px) {
  .navbar {
    width: 80vw;
    padding: 0px 1vw 0px 1vw;
  }
}

.miniMenu {
  display: none;
  position: absolute;
  left: 0px;
  width: 100vw;
  top: 87px;
  background-color: #ffffff;
  padding: 0px 1.5rem;
  padding-bottom: 20px;
  transition: all 0.3s ease-in-out;
  z-index: 1;
  box-shadow: 0px 8px 10px #0000000d;
}

.miniMenuOn {
  display: block;
}

.miniMenu > ul {
  padding-top: 18px;
}

.miniMenu > ul li {
  display: block;
  padding: 8px;
  margin-left: -7vw;
  text-align: left;
  font: normal normal bold 14px/24px Open Sans;
  letter-spacing: 1.4px;
  color: #1ba6e1;
  text-transform: uppercase;
}

.miniMenu > ul li a {
  text-decoration: none;
  color: #1ba6e1;
}

.miniMenu > ul li a:hover {
  text-decoration: none;
  color: #1ba6e1;
}

.miniMenuBtn {
  display: block;
  margin-top: 5rem !important;
}

/* Search */
.miniSearch {
  display: none;
  position: absolute;
  left: 0px;
  width: 100vw;
  padding: 4vw;
  top: 87px;
  background-color: #e3f2f8;
  padding-bottom: 20px;
  transition: all 0.3s ease-in-out;
  z-index: 1;
  box-shadow: 0px 8px 10px #0000000d;
}

.miniSearchOn {
  display: block;
}

.searchbox {
  margin-top: 1.2rem;
  margin-bottom: 0;
}

.formcontrol {
  height: 40px !important;
  border: none !important;
  border-right: none !important;
  padding: 2px 16px !important;
  font-size: 0.82rem !important;
}

.formcontrol:focus {
  outline: none !important;
  box-shadow: none !important;
}

.searchIcon {
  color: #1ba6e1 !important;
  border: none !important;
  border-left: none !important;
  background-color: #ffffff !important;
  border-radius: 4px !important;
  border-bottom-left-radius: 0px !important;
  border-top-left-radius: 0px !important;
}

.actionMenu {
  margin-left: -50px;
}

.actionMenuDropdown {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.actionMenuDropdownContent {
  display: block;
  position: absolute;
  background-color: #ffffff;
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
  padding: 12px 0px;
  z-index: 101;
  width: max-content;
  border-radius: 4px;
  margin-left: 0px;
}

@media (min-width: 768px) {
  .actionMenuDropdownContent {
    display: block;
    position: absolute;
    background-color: #ffffff;
    box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
    padding: 12px 0px;
    z-index: 101;
    width: max-content;
    border-radius: 4px;
    margin-left: -100px;
  }
}

.actionBtn {
  border: none;
  display: block;
  text-align: left;
  outline: none;
  color: #1ba6e1;
  background-color: #ffffff;
  transition: all 0.3s ease-in-out;
  letter-spacing: 1.4px;
  padding: 4px 16px;
  font: normal normal bold 14px/24px Open Sans;
}

.actionBtn:hover {
  background-color: #fafafa;
}

.actionBtn:focus {
  border: none;
  outline: none;
  background-color: #fafafa;
}

.linkBtn {
  background-color: transparent;
  outline: none !important;
  border: none !important;
  font-size: inherit;
  color: inherit;
  font-weight: inherit;
  padding: 2px 0px;
  margin-bottom: 4px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
}

.linkBtn:focus {
  outline: none !important;
}
