.root {
  display: flex;
}

.searchbox {
  background-color: #e3f2f8;
  border-radius: 8px !important;
  opacity: 1;
  border-right: 0px !important;
}

.searchbox .input-group-text {
  background-color: white;
  border-radius: 8px !important;
  border-left: 0px !important;
}

.formcontrol {
  padding: 1rem 0rem 1rem 0.25rem;
  outline: none !important;
  border-radius: 10px !important;
  border-top-right-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
  border-right: none !important;
  box-shadow: none !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.sortButton {
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  font: normal normal 600 14px/24px Open Sans;
  padding: 4px 8px;
  background-color: #ffffff;
  outline: none;
  margin-top: 2px;
}

.sortButton:focus {
  outline: none !important;
}

.dropdowncontent {
  display: block;
  width: 300px;
  position: absolute;
  top: 45px;
  right: 0px;
  background-color: #ffffff;
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
  padding: 12px 0px;
  z-index: 101;
}

.linkbtn {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 3px solid #dededf !important;
  border-radius: 10px;
  opacity: 1;
  padding: 4px;
  width: 100%;
  height: auto;
  background-color: "white";
}

.linkbtn:focus {
  outline: none !important;
}

.icons {
  width: 100%;
  color: #a2a2a2 !important;
}

.pointer,
.miniPointer {
  display: block;
  position: absolute;
  top: -8px;
  right: 3.7rem;
  z-index: 100 !important;
  height: 16px;
  background-color: #ffffff;
  width: 16px;
  transform: rotate(45deg);
}

.miniPointer {
  border-top: solid thin #e5e5e5;
  border-left: solid thin #e5e5e5;
  transform: translateX(-250%) rotate(45deg);
}

.formcontrol:focus {
  outline: none !important;
  box-shadow: none !important;
  border: 1px solid #ced4da !important;
  border-right: none !important;
}

.head {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000cc;
  opacity: 1;
  margin-top: 10px;
}

.cancelBtn {
  text-decoration: underline !important;
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #454545 !important;
  opacity: 0.5;
  font-weight: 700;
  border: none;
  margin-left: -10px;
  outline: none !important;
}

.cancelBtn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.dropdown {
  float: right;
  margin-right: 1rem;
  margin-top: 8px;
}

.btns {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
  outline: none !important;
}

.btns:focus {
  outline: none !important;
  box-shadow: none !important;
}

.breadcolor {
  font: normal normal normal 14px/24px Roboto !important;
  letter-spacing: 0px;
  color: #1ba7e2 !important;
  opacity: 1;
  margin-bottom: 0px !important;
}

.breadcol {
  font: normal normal normal 14px/24px Roboto !important;
  letter-spacing: 0px;

  opacity: 1;
}
.sortby {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

.bread ol {
  background-color: transparent !important;
  margin-bottom: 0px;
}

.homebtn {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #1ba6e1 !important;
  opacity: 0.5;
  font-weight: 700;
  border: none;
  outline: none !important;
  margin-left: -4px;
}

.articlebtn {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000 !important;

  font-weight: 700;
  border: none;
  outline: none !important;
  margin-left: -4px;
}

.checkbox:checked {
  background-color: white !important;
}

.checkbox > label {
  text-transform: capitalize;
  font: normal normal 600 14px/24px Open Sans;
  opacity: 1;
}

.checkbox > input {
  margin-top: 6px;
}

.new {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

.sort {
  font: normal normal normal 14px/19px Open Sans;
  letter-spacing: 0px;
}

.menu {
  width: 150% !important;
  transform: translate(-30%, 60px) !important ;
}

.option {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

.stickyFilters {
  padding: 0px;
}

@media (min-width: 768px) {
  .homebtn {
    font: normal normal 600 14px/24px Open Sans;
    letter-spacing: 0px;
    color: #1ba6e1 !important;
    opacity: 0.5;
    font-weight: 700;
    border: none;
    outline: none !important;
    float: left;
  }

  .articlebtn {
    font: normal normal 600 14px/24px Open Sans;
    letter-spacing: 0px;
    color: #000000 !important;

    font-weight: 700;
    border: none;
    outline: none !important;
  }
  .cont {
    position: relative;
  }

  .formcontrol {
    padding: 0rem 1rem;
    outline: none !important;
    box-shadow: none !important;
  }

  .stickyFilters {
    position: -webkit-sticky;
    position: sticky;
    top: 95px;
  }
}

.searchIcon {
  border-radius: 10px !important;
  border-bottom-left-radius: 0px !important;
  border-top-left-radius: 0px !important;
  background-color: #ffffff !important;
  border-left: none !important;
}

.activeSort {
  font-weight: 700;
  margin-left: 8px;
  margin-right: 16px;
}

.categoryMenuBtn {
  position: relative;
}

.catergoryMiniMenu {
  position: absolute;
  top: 0px;
  display: block;
  right: 0px;
  z-index: 1002;
  background-color: #ffffff;
}

.showSort {
  position: relative;
}

.showMiniSort {
  position: absolute;
  top: 0px;
  display: block;
  right: 0px;
  z-index: 1002;
  background-color: #ffffff;
}

.sortMiniDropdowncontent {
  position: absolute;
  width: 100%;
  left: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  background: white;
}

.sortMiniDropdowncontent div {
  display: block;
}

.miniSortMenuList {
  width: 90%;
  border: 1px solid #e5e5e5;
  box-shadow: 0px 3px 20px #00000029;
  border-radius: 4px;
}

.activeSortOp {
  font-weight: bold !important;
  color: #000000 !important;
}

.miniSortMenuList > div {
  display: block !important;
}

.miniPointerFilter {
  display: block;
  position: absolute;
  top: -8px;
  right: 0rem;
  z-index: 100 !important;
  height: 16px;
  background-color: #ffffff;
  width: 16px;
  transform: rotate(45deg);
}

.miniPointerFilter {
  border-top: solid thin #e5e5e5;
  border-left: solid thin #e5e5e5;
  transform: translateX(-250%) rotate(45deg);
}

.btnOutlined {
  border: 3px solid #1ba7e2 !important;
}

.iconOutlined {
  color: #1ba7e2 !important;
}

.horizontalLine {
  margin-top: 4rem;
  margin-bottom: -2rem;
  border: 0;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
}
