.container {
  padding: 2rem;
  margin-top: 65px;
}

.pageTitle {
  font: normal normal 500 30px/41px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
  font-size: 1.6rem;
}

.editor {
  border: 1px solid #f1f1f1;
  padding: 0px 8px;
  border-radius: 4px;
  font-weight: 500;
  min-height: 20rem;
}

.toolbar {
  border-radius: 4px;
}

.editorWrapper {
  padding: 4px;
  border: 1px solid #ced4da;
  border-radius: 4px;
}

.imagePreview {
  height: 8rem;
  width: auto;
}

.required {
  display: inline-block;
  margin: 0px 2px;
  color: red;
}

.errorMsg {
  color: red;
  margin: 4px 0px;
}

.success {
  color: green;
  margin: 4px 0px;
}

.submitBtn {
  margin-top: 4px !important;
  font-weight: 400;
}
