.logo {
  margin: 20px auto;
  max-width: 80px;
  display: block;
}

@media (min-width: 768px) {
  .logo {
    margin: 20px 0;
  }
}

.footer {
  margin-top: 2rem;
  padding: 2rem 0;
  margin-bottom: 60px;
  background: #fff;
  /* border-top: 1px solid #e5e5e5; */
}
.footer_links {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.footer_links li a {
  font: normal normal bold 14px/24px Open Sans;
  color: #000000;
  text-transform: uppercase;
  /* letter-spacing: 1px;  */
  font-size: 0.8rem;
  font-weight: 700 !important;
  /* padding: 0.5rem 0.5rem; */
}

@media (min-width: 768px) {
  .footer_links {
    float: right;
  }
}

.footer_links li {
  display: inline;
  padding: 0.5rem 0.5rem;
}

@media (min-width: 768px) {
  .footer_links li {
    display: inline;
    padding: 0.5rem 1rem;
  }

  .footer_links li a {
    font: normal normal bold 14px/24px Open Sans;
    color: #000000;
    text-transform: uppercase;
    /* letter-spacing: 1px;  */
    font-size: 0.875rem;
    font-weight: 700 !important;
    /* padding: 0.5rem 0.5rem; */
  }
}

.footer_links li a:hover {
  text-decoration: none;
}

.copyrights {
  text-align: center;
  color: #9b9b9b;
  font-weight: 400;
  border-top: 1px solid #e5e5e5;
  margin-top: 30px;
  padding-top: 10px;
}

.tandc {
  display: block;
}

@media (min-width: 768px) {
  .copyrights {
    text-align: right;
  }

  .tandc {
    display: initial;
  }
}
