.label {
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: 600;
  font-stretch: normal;
  font-size: 13px;
  line-height: 18px;
  color: #333333de;
  opacity: 1;
  font-family: "Open Sans";
}

.span {
  background-color: transparent;
}

.btn {
  background-color: #1ba7e2 !important;
  border-color: #1ba7e2 !important;
  margin-top: 50px !important;
}

.pageTitle {
  font-weight: 500;
  color: black;
  text-align: left;
  margin-top: 5px;
  margin-bottom: 20px;
  font: normal normal 600 20px/36px Open Sans;
}

@media (min-width: 768px) {
  .pageTitle {
    text-align: center;
    font-size: 26px;
  }
}

.errorMessage {
  color: red;
  font-size: 0.8rem;
  font-weight: 400;
  margin-top: -10px;
  padding: 0px 3px;
}
