.root {
  display: flex;
}

.searchbox {
  background-color: #e3f2f8;
  border-radius: 8px !important;
  opacity: 1;
  border-right: 0px !important;
}

.searchbox .input-group-text {
  background-color: white;
  border-radius: 8px !important;
  border-left: 0px !important;
}

.sectiontitle {
  margin-top: 2rem;
  position: relative;
}
.sectiontitle h2 {
  font: normal normal 600 30px/41px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
  font-size: 1.25rem;
}

@media (min-width: 768px) {
  .sectiontitle {
    margin-top: 4.5rem;
    position: relative;
  }
  .sectiontitle h2 {
    font-size: 2rem;
  }
}

.formcontrol {
  padding: 1rem 0rem 1rem 0.25rem;
  outline: none !important;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: none !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.sortButton {
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  font: normal normal 600 14px/24px Open Sans;
  padding: 4px 8px;
  background-color: #ffffff;
  outline: none;
  margin-top: 2px;
}

.sortButton:focus {
  outline: none !important;
}

.bookmark {
  font: normal normal medium 10px/18px Roboto !important;
  opacity: 0.7;
  letter-spacing: 0.7px;
  color: #9b9b9b;
  font-size: 0.625rem;
  line-height: 1.125rem;
  background-color: transparent;
  border: none;
  outline: none !important;
}

@media (min-width: 768px) {
  .bookmark {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}
.bookmark svg {
  position: relative;
  top: -3px;
}

.bookmark:hover {
  text-decoration: none;
  color: #007bff !important;
}

.buttonsgroup {
  font: normal normal normal 14px/22px Roboto;
  text-align: left;
  letter-spacing: 0px;
  color: #000000;
  margin-right: 8px;
  opacity: 1;
  background-color: #c4c4c4;
  border: none;
  outline: none !important;
}

@media (min-width: 768px) {
  .buttonsgroup {
    font-size: 0.875rem;
  }
}

.buttonsgroup:hover {
  outline: none !important;
  text-decoration: none;
  color: #000000 !important;
  background-color: #c4c4c4;
}

.buttonsgroup:focus {
  outline: none !important;
}

.bookmarkup {
  font: normal normal medium 10px/18px Roboto !important;
  opacity: 0.7;
  letter-spacing: 0.7px;
  color: #9b9b9b;
  font-size: 0.625rem;
  line-height: 1.125rem;
  background-color: transparent;
  border: none;
  outline: none !important;
  float: right;
}

@media (min-width: 768px) {
  .bookmarkup {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}
.bookmarkup svg {
  position: relative;
  top: -3px;
}

.bookmarkup:hover {
  text-decoration: none;
  color: #007bff !important;
}

.dropdowncontent {
  display: block;
  width: 300px;
  position: absolute;
  top: 45px;
  right: 0px;
  background-color: #ffffff;
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.1);
  padding: 12px 0px;
  z-index: 101;
}

.linkbtn {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #dededf !important;
  border-radius: 8px;
  opacity: 1;
  background-color: "white";
}

.linkbtn:focus {
  color: blue;
  outline: none !important;
}

.icons {
  color: grey !important;
  opacity: 0.5;
}

.pointer,
.miniPointer {
  display: block;
  position: absolute;
  top: -8px;
  right: 10px;
  z-index: 100 !important;
  height: 16px;
  background-color: #ffffff;
  width: 16px;
  transform: rotate(45deg);
}

.miniPointer {
  border-top: solid thin #4e4e4e88;
  border-left: solid thin #4e4e4e88;
  transform: translateX(-250%) rotate(45deg);
}

.formcontrol:focus {
  outline: none;
  box-shadow: none;
}

.head {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000cc;
  opacity: 1;
  margin-top: 10px;
}

.cancelBtn {
  text-decoration: underline !important;
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #454545 !important;
  opacity: 0.5;
  font-weight: 700;
  border: none;
  margin-left: -10px;
}

.cancelBtn:focus {
  outline: none;
  box-shadow: none;
}

.dropdown {
  float: right;
  margin-right: 1rem;
  margin-top: 8px;
}

.btns {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

.breadcolor {
  font: normal normal normal 14px/24px Roboto !important;
  letter-spacing: 0px;
  color: #1ba7e2 !important;
  opacity: 1;
  margin-bottom: 0px !important;
}

.breadcol {
  font: normal normal normal 14px/24px Roboto !important;
  letter-spacing: 0px;

  opacity: 1;
}
.sortby {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

.bread ol {
  background-color: transparent !important;
  margin-bottom: 0px;
}

.homebtn {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #1ba6e1 !important;
  opacity: 0.5;
  font-weight: 700;
  border: none;
  outline: none !important;
  margin-left: -4px;
}

.articlebtn {
  font: normal normal 600 14px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000 !important;

  font-weight: 700;
  border: none;
  outline: none !important;
  margin-left: -4px;
}

.checkbox:checked {
  background-color: white !important;
}

.new {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

.sort {
  font: normal normal normal 14px/19px Open Sans;
  letter-spacing: 0px;
}

.menu {
  width: 150% !important;
  transform: translate(-30%, 60px) !important ;
}

.option {
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

@media (min-width: 768px) {
  .homebtn {
    font: normal normal 600 14px/24px Open Sans;
    letter-spacing: 0px;
    color: #1ba6e1 !important;
    opacity: 0.5;
    font-weight: 700;
    border: none;
    outline: none !important;
    float: left;
  }
  .articlebtn {
    font: normal normal 600 14px/24px Open Sans;
    letter-spacing: 0px;
    color: #000000 !important;

    font-weight: 700;
    border: none;
    outline: none !important;
  }
  .cont {
    position: relative;
  }

  .formcontrol {
    padding: 0rem 1rem;
  }
}

.searchIcon {
  border-radius: 4px !important;
  border-bottom-left-radius: 0px !important;
  border-top-left-radius: 0px !important;
  background-color: #ffffff !important;
  border-left: none !important;
}

.activeSort {
  font-weight: 700;
  margin-left: 8px;
  margin-right: 16px;
}

.categoryMenuBtn {
  position: relative;
}

.catergoryMiniMenu {
  position: absolute;
  top: 0px;
  display: block;
  right: 0px;
  z-index: 1002;
  background-color: #ffffff;
}

.showSort {
  position: relative;
}

.showMiniSort {
  position: absolute;
  top: 0px;
  display: block;
  right: 0px;
  z-index: 1002;
  background-color: #ffffff;
}

.sortMiniDropdowncontent {
  position: absolute;
  width: 100%;
  left: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  background: white;
}

.miniSortMenuList {
  width: 80%;
  border: solid thin #4e4e4e88;
}

.curated {
  margin: 0px;
}

@media (min-width: 768px) {
  .curated {
    float: right;
    margin-right: 250px;
  }
}

.fontstylesmain {
  text-align: left;
  font: normal normal medium 12px/18px Roboto;
  letter-spacing: 1.2px;
  color: #707070;
  opacity: 0.5;
}

.fontstylesmiddle {
  font: normal normal bold 14px/18px Roboto;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.7;
}

.fontstylesbottom {
  text-align: left;
  font: normal normal normal 14px/18px Roboto;
  letter-spacing: 0px;
  color: #000000;
  opacity: 0.5;
}

.horizontalLine {
  margin-top: 4rem;
  margin-bottom: -2rem;
  border: 0;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
}
