.label {
  font-style: normal;
  font-variant-ligatures: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-variant-east-asian: normal;
  font-weight: 600;
  font-stretch: normal;
  font-size: 13px;
  line-height: 18px;
  color: #333333de;
  opacity: 1;
}

.field {
  background-color: transparent !important;
}

.errorMessage {
  color: red;
  font-size: 0.8rem;
  font-weight: 400;
  margin-bottom: 8px;
  margin-top: -8px;
  padding: 0px 3px;
}

.pageTitle {
  font-weight: 500;
  color: black;
  text-align: left;
  margin-top: 5px;
  margin-bottom: 20px;
  font: normal normal 600 20px/36px Open Sans;
}

@media (min-width: 768px) {
  .pageTitle {
    text-align: center;
    font-size: 26px;
  }
}

.signUpBtn {
  width: 100% !important;
  background-color: #1ba7e2 !important;
  border: none !important;
}

.selectBlock {
  display: block !important;
}

@media (min-width: 768px) {
  .signUpBtn {
    width: 20rem !important;
  }

  .selectBlock {
    display: inline-block !important;
  }
}
