.page_title {
  font-size: 20px;
  margin-top: 40px;
  margin-bottom: 25px;
  font-weight: 600;
}

.section_desc {
  font-size: 16px;
  text-align: justify;
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
  padding: 0px 6px;
}

ol {
  padding-left: 1.2rem;
  text-align: justify;
  font: normal normal normal 16px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

@media (min-width: 768px) {
  .page_title {
    font-size: 24px;
  }
}

.horizontalLine {
  margin-top: 2rem;
  margin-bottom: -2rem;
  border: 0;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
}
