.articlecard {
  margin: 1rem 0;
  /* min-width: 200px; */
}

@media (min-width: 768px) {
  .articlecard {
    /* margin-top: 0rem;  */
    /* min-width: 200px; */
  }
}

.taxo {
  display: block;
  text-align: left;
  font: normal normal bold 10px/24px Roboto;
  letter-spacing: 1px;
  color: #f2541a;
  text-transform: uppercase;
  opacity: 1;
  font-weight: 700;
  margin: 0;
  margin-bottom: 0.4rem;
  margin-top: 1rem;
}

@media (min-width: 768px) {
  .taxo {
    font: normal normal bold 10px/24px Roboto;
    letter-spacing: 1px;
    color: #f2541a;
    text-transform: uppercase;
    opacity: 1;
  }
}
.articlecard .taxo:hover {
  text-decoration: none;
}

.imgfluid {
  width: 100%;
  height: 200px !important;
}

@media (min-width: 768px) {
  .imgfluid {
    width: 100%;
    height: 280px !important;
  }
}

.title {
  text-align: justify;
  font: normal normal 600 16px/24px Open Sans;

  letter-spacing: 0px;
  color: #000000;
  text-transform: capitalize;
  opacity: 1;

  text-decoration: none !important;
  outline: none !important;
}

.title:hover {
  text-decoration: none !important;
  outline: 0;
}

.title:focus {
  text-decoration: none !important;
  outline: none !important;
}

@media (min-width: 768px) {
  .title {
    font: normal normal 600 28px/44px Open Sans;
    letter-spacing: 0px;
    color: #000000;
    text-transform: capitalize;
    opacity: 1;
  }
}
.articlecard .author {
  text-align: left;
  font: normal normal normal 12px/18px Roboto;
  letter-spacing: 0px;
  color: #707070;
  opacity: 0.7;
}
@media (min-width: 768px) {
  .articlecard .author {
    font: normal normal normal 12px/18px Roboto;
    letter-spacing: 0px;
    color: #707070;
    opacity: 0.7;
  }
}

.excerpt {
  font: normal normal normal 14px/22px Roboto;
  letter-spacing: 0.7px;
  color: #707070;
  opacity: 1;
  text-align: justify;

  margin-top: 10px;
  margin-bottom: 1rem;
}

.excerpt:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .excerpt {
    font: normal normal normal 14px/22px Roboto;
    letter-spacing: 0.7px;
    color: #707070;
    opacity: 1;
  }
}
.articlecard .footer p {
  margin: 0;
}
.articlecard .footer .left {
  float: left;
}
.articlecard .footer .right {
  float: right;
}

.bookmark {
  font: normal normal medium 10px/18px Roboto !important;
  opacity: 0.7;
  letter-spacing: 0.7px;
  color: #9b9b9b;
  font-size: 0.625rem;
  line-height: 1.125rem;
  background-color: transparent;
  border: none;
  outline: none !important;
}

.articlecard .footer {
  font: normal normal medium 10px/18px Roboto !important;
  opacity: 0.7;
  letter-spacing: 0.7px;
  color: #9b9b9b;
  font-size: 0.625rem;
  line-height: 1.125rem;
  margin-top: 5px;
}
@media (min-width: 768px) {
  .articlecard .footer .bookmark {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
}
.articlecard .footer .bookmark svg {
  position: relative;
  top: -3px;
}

.bookmark:hover {
  text-decoration: none;
  color: #007bff !important;
}
.articlecard .footer .publishedright {
  text-align: right;
  font: normal normal normal 12px/18px Roboto;
  letter-spacing: 0px;
  color: #707070;
  opacity: 0.7;
  margin-top: -20px;
}
@media (min-width: 768px) {
  .articlecard .footer .publishedright {
    font: normal normal normal 12px/18px Roboto;
    letter-spacing: 0px;
    color: #707070;
    opacity: 0.7;
  }
}

.imgheader {
  position: relative;
}

.articlecenter {
  background-color: white !important;
  border-radius: 16px !important;
  border-color: none !important;
  border: 0px !important;
  position: absolute !important;
  top: 10px !important;
  left: 25px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px 8px;
}

.book {
  background-color: white !important;
  border-radius: 8px !important;
  border-color: none !important;
  border: 0px !important;
  position: absolute !important;
  top: 10px !important;
  right: 25px !important;
  width: 33px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 20px #00000029;
  opacity: 1;
}

.book:focus {
  outline: none !important;
}

.art {
  font: normal normal bold 12px/24px Open Sans;
  letter-spacing: 0px;
  color: #000000;
  text-transform: capitalize;
  opacity: 0.5;
  display: inline-block;
  margin-left: 8px;
}

.videoFrame {
  min-height: 24rem;
  height: auto;
  object-fit: cover;
  border: none !important;
  width: 100%;
  background-color: rgb(22, 22, 22);
}

@media (min-width: 768px) {
  .videoContainer {
    transform: scaleX(1.2) !important;
  }
}
