/* Global */
body {
  font: normal normal 600 16px/24px Open Sans;
  font-family: "Open Sans";
}

/* Header */
header {
  padding: 25px 0;
}
.header-title {
  color: #1ba6e1;
  font-size: 25px;
  font-weight: 500;
  font: normal normal 600 25px/34px Open Sans;
  margin-top: 20px;
}

/* Onboarding */
.onboarding-page_title {
  font-size: 25px;
  font-weight: 500;
  font: normal normal 600 26px/36px Open Sans;
  color: black;
  margin-top: 5px;
  margin-bottom: 50px;
}

.onboarding-page_sub_title {
  font: normal normal normal 14px/19px Open Sans;
  margin-top: -40px;
  margin-bottom: 50px;
}
/* Form */

.input-group-prepend .input-group-text {
  background-color: transparent;
}

.input-group {
  outline: none !important;
}

.form-control:focus {
  box-shadow: 0 0 0 0.05rem rgba(0, 123, 255, 0.25);
}

.onboarding-btn {
  background-color: #1ba7e2 !important;
  border-color: #1ba7e2 !important;
  margin-top: 50px;
}

label {
  font: normal normal 600 13px/18px Open Sans;
  color: #333333de;
  font-size: 13px;
}
.mb-3,
.my-3 {
  margin-bottom: 0px;
}
input[type="radio"] {
  font: normal normal normal 14px/24px Open Sans;
  color: #000000cc;
  font-size: 14px;
}
.sign_up_submit_btn {
  width: 342px;
  height: 44px;
}
.form-control {
  outline: none !important;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: transparent !important;
  box-shadow: 0 0 0 0.01rem rgba(0, 123, 255, 0.25);
}

.responsiveForm {
  width: 100%;
}
@media screen and (min-width: 1000px) {
  .responsiveForm {
    width: 70%;
    margin-left: 15% !important;
  }
}

#partitioned {
  padding-left: 15px;
  letter-spacing: 42px;
  border: 0;
  background-image: linear-gradient(
    to left,
    black 70%,
    rgba(255, 255, 255, 0) 0%
  );
  background-position: bottom;
  background-size: 50px 1px;
  background-repeat: repeat-x;
  background-position-x: 35px;
  width: 320px;
  min-width: 320px;
  outline: none;
}

#divInner {
  left: 0;
  position: sticky;
  margin-top: 25px;
}

#divOuter {
  width: 300px;
  overflow: hidden;
  outline: none;
  margin-top: 15px;
}

@media screen and (max-width: 736px) {
  #divOuter {
    margin-left: 40px;
  }
}

/* Input OTP */
.input-otp {
  display: flex;

  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
.input-otp .input-wrapper {
  flex: 0 0 16.667%;
  max-width: 16.667%;

  padding: 0 15px;
}
.input-otp input {
  border: none;
  border-bottom: 1px solid #8b8b8b;
  text-align: center;
  width: 100%;
}
.input-otp input:focus {
  outline: none;
}
